   <!--<h1  style="color:white;">搜索结果  <span style="font-size: 16px;"> 共:{{ showlist.length }}条</span></h1>-->

     <!-- pc端 -->
    <div class="main-content" >
      <ul   class="text-left col-sm-12" style="margin-bottom:20px;border-bottom: 1px solid #F5F5F5;font-size:18px;">
        <span class="col-sm-12 col-xs-12 text-center">更新结果</span>
      </ul>
      <div class="col-sm-12 text-center error" v-if="Error!=0">
        {{ update.Desc }}
      </div>
      <ul  class="text-left col-sm-12" style="margin-bottom:20px;" v-if="result != null && result.length!=0 ">
        <div class="col-sm-12" >
