<script>
  import { mapGetters, mapActions } from 'vuex'
  import Top from './head-active.vue'
   export default {
    name:'filterdetail',
    computed:{
      ...mapGetters({
         update: 'update'
      })
    },

    data() {
        return {
          isInputShow: false,
          params: {},
          size:0,
          smarttime:0,
          smarttimeend:0,
          current:1,
          showItem:5,
          allpage:1,
          showlist:[],
          selectBy:'',
          id:'',
          namespace:'',
          allnum:'',
          showFlag:[],
          key:'',
          appIDFlag:false,
          result:'',
          Desc:'',
          Error:0,
          body:{
//          "Services": {
//            "publiccredit": "http://192.168.1.242:8080/v1/publiccredit/",
//            "judicialcredit": "http://192.168.1.242:8080/v1/judicialcredit/",
//            "publicservice": "http://192.168.1.242:8080/v1/publicservice/",
//            "financecedit": "http://192.168.1.242:8080/v1/financecedit/",
//            "other": "http://192.168.1.242:8080/v1/other/"
//          },
//          "Name": "gui yang identity"
          }
        }
    },
    methods:{
      back:function(){
        this.$router.push({'path': '/browser'})
      },
      testsearch:function(){
        var publiccredit=this.publiccredit
        var judicialcredit=this.judicialcredit
        var publicservice=this.publicservice
        var financecedit=this.financecedit
        var other=this.other
        var name=this.name
        this.body={
          "Services":{
            "publiccredit": publiccredit,
            "judicialcredit": judicialcredit,
            "publicservice": publicservice,
            "financecedit": financecedit,
            "other": other
          },
           "Name": name
        }
//      console.log(JSON.stringify(this.body))
        var params={}
        if(this.addr!=null && this.addr!=""){
          params.addr =this.addr
          params.services =this.body.Services
          params.name=this.body.Name
          this.$store.dispatch('update',params)
        }else{
          
        }
        
      }
    },
    watch:{
      'update':function(){
        this.result= this.update.Result
        this.Error=this.update.Error
//      if(this.result!=null && this.result.length!=0){
//        this.services=JSON.parse(this.result.services)
//      }

      }
    },
    components: {
      Top
    },
    beforeDestroy() {
      this.nameSpace = [];
    }
  }
</script>

